.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.ornaments-wrapper {
		overflow: hidden;
		border-radius: var(--border-radius);
		.bg-1 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 100%;
			.image-element {
				height: 100%;
				.img-fluid {
					height: 100%;
					object-fit: cover;
					object-position: 50% 0%;
				}
			}
		}
	}
	.appology-wrap {
		position: relative;
		padding: 3rem 2rem;
		border-radius: var(--border-radius);
		@include max-md {
			padding: 2rem 1rem;
		}
	}
}
